#box {
    width: 100%;
    display: flex;
    justify-content: space-between;

    .regist-list {
        width: 100%;
        height: 930px;

        .title {
            width: 100%;
            height: 40px;
            padding: 20px;

            .logo-warp {
                float: left;
                width: 154px;
                height: 40px;
                font-family: "MiSans", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
                font-variant: tabular-nums;
                color: rgb(51, 51, 51);
                font-size: 26px;
                font-weight: 500;

                img {
                    width: 40px;
                    height: 40px;
                    vertical-align: middle;
                    cursor: pointer;
                }
            }

            .nav-wrap {
                float: right;
                height: 40px;
                width: 420px;

                ul li {
                    cursor: pointer;
                    float: left;
                    height: 40px;
                    margin: 0 10px;
                    font-size: 14px;
                    color: rgb(131, 131, 131);
                    line-height: 40px;

                    &:hover {
                        color: #ff6900;
                        ;
                    }
                }

            }
        }

        .user-regist {
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            width: 360px;
            padding: 40px 45px;
            box-shadow: 0 20px 50px 0 hsl(0deg 0% 64% / 10%);
            margin: 0 auto 80px;
            height: 500px;
            position: relative;
            .changeWeb {
                width: 360px;
                height: 41px;
                position: relative;

                .regist-title {
                    font-weight: 600;
                    color: black;
                }

                span {
                    position: absolute;
                    background: #ff5c00;
                    left: 64px;
                    width: 44px;
                    top: 38px;
                    border-radius: 2px;
                    height: 4px;
                }
            }

            .user-title {
                cursor: pointer;
                float: left;
                margin-right: 20px;
                width: 44px;
                height: 44px;
                line-height: 44px;
                font-weight: 400;
                font-size: 22px;
                color: rgb(187, 187, 187);
                font-family: "MiSans", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
            }

            input {
                width: 316px;
                height: 60px;
                padding: 0 20px;
                border: none;
                background: rgb(0 0 0 / 3%);
                border-radius: 5px;
                appearance: none;
                font-size: 17px;
                color: #333;
                line-height: 20px;
                border-collapse: separate;
                border-spacing: 0;
            }

            .regist {
                width: 360px;
                margin-top: 20px;
                margin-bottom: 10px;
                background-color: #ff5d0170;
                text-align: center;
                border: none;
                padding: 0 20px;
                border-radius: 4px;
                font-size: 18px;
                font-weight: 400;
                line-height: 60px;
                line-height: 60px;
                color: white;
                font-family: Arial;
            }
        }
        .warning{
            font-size: 12px;
            color: red;
            position: absolute;
            top: 250px;
            left: 45px;
            display: none;
        }
    }

    .regist-background {
        width: 375px;
        background-image: url(../img/regist-background.png);
        background-size: 100% 100%;
    }
}